.compare {
  display: grid;
  row-gap: 48px;

  &__products {
    @include page-grid;

    row-gap: 40px;
  }

  &__product {
    grid-column: 1 / -1;

    @include on-tablet {
      grid-column: 2 / 6;
    }

    @include on-desktop {
      grid-column: span 4;
    }

    @include hover (transform, scale(1.03))
  }
}
